.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}

.header-bg{
    background-color: rgba(0, 0, 0, 0.85);
    transition: 0.5s;
}

.logo {
    height: 80px;
    padding-left: 50%;
}

.nav-men{
    display: flex;
}

.nav-men li{
    padding: 0 1rem;
}

.nav-men li a{
    font-size: 1.2rem;
    font-weight: 500;
}

.hamburger{
    display: none;
}

@media screen and (max-width: 1040px){
    .nav-men{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.9) ;
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
    }

    .nav-men.active{
        left: 0;
    }

    .nav-men li{
        padding: 1rem 0;
    }

    .nav-men li{
        font-size: 2rem;
    }

    .hamburger{
        display: initial;
    }
}